/* Global background reset */
html, 
body,
#root {
  margin: 0 !important;
  padding: 0 !important;
  overflow-x: hidden !important;
  min-height: 100vh !important;
  min-width: 100% !important;
  background-color: transparent !important;
  color: white !important;
}

/* Remove conflicting duplicate class definition and keep only one definition for pixel-space-background */
.pixel-space-background,
canvas.pixel-space-background {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  width: 100vw !important;
  height: 100vh !important;
  z-index: 0 !important; /* Changed from -1 to 0 */
  background-color: transparent !important;
  image-rendering: pixelated !important; /* Crisp pixel rendering */
  pointer-events: none !important;
  visibility: visible !important;
  display: block !important;
  
  /* Hardware acceleration */
  transform: translateZ(0);
  backface-visibility: hidden;
  will-change: transform;
}

/* Pixel space background with high specificity */
.pixel-space-background {
  position: relative !important;
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  z-index: -1 !important; /* Ensure it stays behind content */
  width: 100vw !important;
  height: 5150px !important;
  pointer-events: none !important;
}

/* Canvas container (if there is one) */
.pixel-space-container {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  width: 100% !important;
  height: 100vh !important;
  z-index: 0 !important;
  overflow: hidden !important;
  margin: 0 !important;
  padding: 0 !important;
  background-color: transparent !important;
}

/* Make sure body has a proper background color in case of any gaps */
body {
  background-color: transparent !important;
}

/* Ensure render quality */
canvas.pixel-space-background {
  image-rendering: -webkit-optimize-contrast;
  image-rendering: pixelated;
}

/* Make ALL content sections transparent - very high specificity */
body .page-container,
body .home,
body .content-wrapper,
body .section,
body header, 
body main, 
body footer, 
body div[role="main"],
body div[class*="content"],
body .EXPERIENCE,
body .ABOUT-ME,
body .RESUMÉ,
body .PROJECTS-OVERVIEW,
body .c-f-CROZIER,
body .BB-TECHNOSOL,
body .EXCO {
  background-color: transparent !important;
  background-image: none !important;
}

/* Prevent any background colors in other major components */
header, 
footer, 
div[class*="content"] {
  background-color: transparent !important;
}

/* Add media queries for different device sizes */
@media (max-width: 1200px) {
  .pixel-space-background,
  canvas.pixel-space-background {
    width: 100vw !important;
    height: 100vh !important;
  }
}

@media (max-width: 768px) {
  .pixel-space-background,
  canvas.pixel-space-background {
    width: 100vw !important;
    height: 100vh !important;
  }
}

/* For very small devices */
@media (max-width: 480px) {
  .pixel-space-background,
  canvas.pixel-space-background {
    width: 100vw !important;
    height: 100vh !important;
  }
}
