.HEADER {
  background-color: #000000;
  height: calc(80px * 1.1); /* Taller for desktop */
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid var(--mainprimary);
  box-shadow: 0 0 10px var(--mainprimary);
  position: relative;
}

.nav-container {
  width: 100%;
  max-width: calc(1200px * 1.1); /* Wider for desktop */
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav-item {
  color: #FFFFFF;
  font-family: 'Press Start 2P', monospace;
  font-size: calc(16px * 1.1); /* Larger for desktop */
  text-decoration: none;
  padding: 0 calc(25px * 1.1); /* More spacing for desktop */
  text-align: center;
  transition: color 0.3s ease;
}

.nav-item:hover, 
.nav-item.active {
  color: #589E00;
}

/* Tablet devices */
@media screen and (max-width: 992px) {
  .HEADER {
    height: calc(70px * 1.1);
  }
  
  .nav-container {
    max-width: calc(800px * 1.1);
  }
  
  .nav-item {
    font-size: calc(14px * 1.1);
    padding: 0 calc(20px * 1.1);
  }
}

/* Small tablets and large phones */
@media screen and (max-width: 768px) {
  .HEADER {
    height: calc(60px * 1.1);
  }
  
  .nav-container {
    padding: 0 calc(10px * 1.1);
    justify-content: space-around;
  }
  
  .nav-item {
    font-size: calc(11px * 1.1);
    padding: 0 calc(10px * 1.1);
  }
}

/* Mobile phones */
@media screen and (max-width: 480px) {
  .HEADER {
    height: auto;
    padding: calc(10px * 1.1) 0;
  }
  
  .nav-container {
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .nav-item {
    font-size: calc(9px * 1.1);
    margin: calc(5px * 1.1);
    padding: 0 calc(5px * 1.1);
  }
}