/* Base CSS reset and global styles */
:root {
  --background-dark: #1a1a1a;
  --text-green: #4CAF50;
  --text-white: #ffffff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  image-rendering: auto; /* Smooth image rendering */
}

/* Import Press Start 2P font */
@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

/* Global styles - DESKTOP FIRST */
html, body, #root {
  background-color: #000000 !important;
  margin: 0;
  padding: 0;
  color: white;
  height: 100%;
  width: 100%;
  font-family: 'Press Start 2P', cursive;
  font-size: calc(1rem * var(--scale-factor)); /* 2x larger base font size */
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-font-smoothing: antialiased; /* Smoother text rendering */
  -moz-osx-font-smoothing: grayscale; /* Smoother text rendering */
}

/* App container styling - Desktop default */
.app-container {
  min-height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
  overflow: hidden;
}

/* Ensure content is visible against background */
.home {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  overflow: visible;
}

/* Prevent any other containers from creating scrollbars */
.page-container, .content-wrapper, .section {
  overflow: visible !important;
}

/* Ensure sections are visible - Desktop default */
section {
  width: 100%;
  max-width: calc(1200px * var(--scale-factor)); /* 2x wider sections */
  margin: 0 auto;
  padding: calc(20px * var(--scale-factor));
}

/* Image scaling and pixel density reduction */
img {
  transform: scale(var(--scale-factor));
  image-rendering: pixelated; /* Reduce perceived pixel density */
  filter: brightness(1.05) contrast(0.95); /* Soften the appearance of pixels */
  transition: transform 0.3s ease;
}

/* Scale icons by the same factor */
.icon, svg {
  transform: scale(var(--scale-factor));
  transition: transform 0.3s ease;
}

/* Only apply these styles for tablets and below */
@media (max-width: 992px) {
  :root {
    --text-heading-1-font-size: calc(36px * var(--scale-factor));
    --text-heading-2-font-size: calc(28px * var(--scale-factor));
    --text-heading-3-font-size: calc(20px * var(--scale-factor));
    --text-body-text-font-size: calc(16px * var(--scale-factor));
  }
  
  section {
    max-width: 90%;
  }
}

/* Only apply these styles for medium tablets and below */
@media (max-width: 768px) {
  :root {
    --text-heading-1-font-size: calc(30px * var(--scale-factor));
    --text-heading-2-font-size: calc(24px * var(--scale-factor));
    --text-heading-3-font-size: calc(18px * var(--scale-factor));
    --text-body-text-font-size: calc(14px * var(--scale-factor));
  }
  
  .projects-container {
    flex-direction: column;
    align-items: center;
  }

  .project-card {
    width: 100%;
    max-width: 400px;
  }
}

/* Only apply these styles for mobile */
@media (max-width: 480px) {
  :root {
    --text-heading-1-font-size: calc(24px * 0.8);
    --text-heading-2-font-size: calc(20px * 0.8);
    --text-heading-3-font-size: calc(16px * 0.8);
    --text-body-text-font-size: calc(12px * 0.8);
    --scale-factor: 1.6; /* Slightly reduced scale factor for mobile */
  }
  
  section {
    padding: 15px;
    max-width: 100%;
  }
  
  img, .icon, svg {
    transform: scale(1.6); /* Match the mobile scale factor */
  }
  
  .projects-overview {
    padding: 1rem;
  }

  .project-card .icon {
    width: 200px;
    height: 200px;
  }
}

/* Adjust for very small screens */
@media (max-width: 360px) {
  :root {
    --scale-factor: 1.4; /* Even smaller scale factor for tiny screens */
  }
  
  img, .icon, svg {
    transform: scale(1.4); /* Match the tiny screen scale factor */
  }
}

body {
  width: 100%; 
  min-width: auto;
  background-color: #000000 !important; /* Ensure black background */
}

/* Force desktop view on large screens */
@media (min-width: 993px) {
  .mobile-only {
    display: none !important;
  }
}

/* Force mobile view on small screens */
@media (max-width: 992px) {
  .desktop-only {
    display: none !important;
  }
}