.FOOTER {
  background-color: transparent;
  width: 100%;
  padding: calc(30px * 1.1) 0;
  margin-top: calc(30px * 1.1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  left: 0;
  right: 0;
}

.FOOTER .text-wrapper {
  color: var(--mainprimary);
  font-family: var(--text-heading-3-font-family);
  font-size: calc(var(--text-heading-3-font-size) * 1.1);
  font-style: var(--text-heading-3-font-style);
  font-weight: var(--text-heading-3-font-weight);
  letter-spacing: var(--text-heading-3-letter-spacing);
  line-height: var(--text-heading-3-line-height);
  position: relative;
  white-space: nowrap;
  margin-bottom: calc(20px * 1.1);
  text-align: center;
}

.FOOTER .social-icons {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: calc(30px * 1.1);
  margin-top: calc(10px * 1.1);
}

.FOOTER .email,
.FOOTER .linkedin,
.FOOTER .github {
  height: calc(40px * 1.1);
  width: calc(40px * 1.1);
  position: relative;
  transition: transform 0.3s ease;
}

.FOOTER .email:hover,
.FOOTER .linkedin:hover,
.FOOTER .github:hover {
  transform: scale(1.2);
}

/* Media queries for responsive design */
@media (max-width: 768px) {
  .FOOTER {
    padding: calc(20px * 1.1) 0;
  }
  
  .FOOTER .text-wrapper {
    font-size: calc(16px * 1.1);
    margin-bottom: calc(15px * 1.1);
  }
  
  .FOOTER .social-icons {
    gap: calc(20px * 1.1);
  }
  
  .FOOTER .email,
  .FOOTER .linkedin,
  .FOOTER .github {
    height: calc(30px * 1.1);
    width: calc(30px * 1.1);
  }
}

@media (max-width: 480px) {
  .FOOTER {
    padding: calc(15px * 1.1) 0;
  }
  
  .FOOTER .text-wrapper {
    font-size: calc(14px * 1.1);
    margin-bottom: calc(10px * 1.1);
  }
  
  .FOOTER .social-icons {
    gap: calc(15px * 1.1);
  }
}